<template>
  <div id="app">
    <div class="header"></div>
    <Name />
    <div class="d-flex flex-row">
      <div class="col-md-auto container ml-5 mr-5">
        <Train />
        <div v-for="row in height" class="row justify-content-center ml-5 mr-5" :key="row">
          <div v-for="col in width" class="col col-md-auto" :key="col">
            <div class="row">
              <div class="col-md-auto rail-vertical-invis"></div>
              <div
                @click="send_button(row, col, 0)"
                class="col-md-auto rail rail-vertical"
                :style="{ 'backgroundColor': setColor(row, col, 0) }"
              ></div>
              <div class="col-md-auto rail-vertical-invis"></div>
            </div>
            <div class="row">
              <div
                @click="send_button(row, col, 3)"
                class="col-md-auto rail rail-horizontal"
                :style="{ 'backgroundColor': setColor(row, col, 3) }"
              ></div>
              <div class="col-md-auto center-box"></div>
              <div
                @click="send_button(row, col, 1)"
                class="col-md-auto rail rail-horizontal"
                :style="{ 'backgroundColor': setColor(row, col, 1) }"
              ></div>
            </div>
            <div class="row">
              <div class="col-md-auto rail-vertical-invis"></div>
              <div
                @click="send_button(row, col, 2)"
                class="col-md-auto rail rail-vertical"
                :style="{ 'backgroundColor': setColor(row, col, 2) }"
              ></div>
              <div class="col-md-auto rail-vertical-invis"></div>
            </div>
          </div>
        </div>
        <button
          type="button"
          @click="send_button(0,0,2)"
          class="btn btn-success btn-lg btn-block mt-2 mb"
        >Go</button>
      </div>

      <div class="flex-grow-1">
        <History  />
        <Chatbox  />
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import { mapState } from "vuex";
import History from "@/components/History.vue";
import Chatbox from "@/components/Chatbox.vue";
import Train from "@/components/Train.vue";
export default {
  name: "Home",
  components: { History, Chatbox, Train },
  data() {
    return {
      directions: [],
      startAnimation: false,
      width: 3,
      height: 3,
      gettingName: false
    };
  },
  computed: {
    ...mapState(['rail', "listOfClickedRails"]),
  },
  methods: {
    send_button(row, col, direction) {
      row -= 1;
      col -= 1;
      var id = (row * this.width + col) * 4 + direction;
      // Socket.sendRail(id);
      this.$socket.emit("railClicked", id);
      console.log("sent");
    },

    find_path() {
      // get info from backend
      this.directions = ["e", "e", "s", "s", "e"];
      this.startAnimation = true;
    },

    setColor(row, col, direction) {
      // console.log("went thru here");
      row -= 1;
      col -= 1;
      var id = (row * this.width + col) * 4 + direction;
      
      if (this.listOfClickedRails[id] != undefined && this.listOfClickedRails != null) {
        return this.listOfClickedRails[id];
      }
      
      return "#FFFFFF";
    }
  },
  watch: {
  },
};
</script>

<style>
.header {
  margin-top: 10px;
}

.box {
  width: 80%;
}

.rail-vertical {
  width: 50px;
  height: 80px;
  border-style: solid;
  border-width: 1.5px;
  padding: 0;
}

.rail-vertical-invis {
  width: 80px;
  height: 80px;
  border-style: hidden;
  border-width: 1.5px;
  padding: 0;
}

.rail-horizontal {
  width: 80px;
  height: 50px;
  border-style: solid;
  border-width: 1.5px;
  padding: 0;
}

.center-box {
  width: 50px;
  height: 50px;
  padding: 0;
}

.rail:hover {
  cursor: pointer;
  background-color: #855e3f;
}

.sidebar {
  width: 600px;
  background-color: #e0e0e0;
}

.history {
  background-color: rgb(177, 177, 255);
  padding: 10px;
}

.chatbox {
  background-color: rgb(255, 255, 177);
  padding: 10px;
}
</style>